import React, { Component } from "react";

import Navbar from "./components/Navbar";
import Services from "./components/Services";
import Features from "./components/Features";
import Descriptions from "./components/Descriptions";
import Pricing from "./components/Pricing";
import Team from "./components/Team";
import Testi from "./components/Testi";
import Started from "./components/Started";
import Blog from "./components/Blog";
import Contact from "./components/Contact";
import SocialMedia from "./components/SocialMedia";
import Footer from "./components/Footer";
import FooterLinks from "./components/FooterLinks";

class App extends Component {

    render() {

        var bg1 = require('./assets/images/wave-shape/wave1.png');
        var bg2 = require('./assets/images/wave-shape/wave2.png');
        var bg3 = require('./assets/images/wave-shape/wave3.png');

        return (
            <div>
                {/* Navbar Component*/}
                <Navbar />

                <section className="section bg-home home-half" id="home" data-image-src="images/bg-home.jpg">
                    <div className="bg-overlay"></div>
                    <div className="display-table">
                        <div className="display-table-cell">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-8 offset-lg-2 text-white text-center">
                                        <h1 className="home-title">We help startups launch their products</h1>
                                        <p className="padding-t-15 home-desc">Etiam sed.Interdum consequat proin vestibulum className at.</p>
                                        <p className="play-shadow margin-t-30 margin-l-r-auto"> </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="wave-effect wave-anim">
                        <div className="waves-shape shape-one">
                            <div className="wave wave-one" style={{ backgroundImage: "url("+bg1+")" }}></div>
                        </div>
                        <div className="waves-shape shape-two">
                            <div className="wave wave-two" style={{ backgroundImage: "url("+bg2+")" }}></div>
                        </div>
                        <div className="waves-shape shape-three">
                            <div className="wave wave-three" style={{ backgroundImage: "url("+bg3+")" }}></div>
                        </div>
                    </div>
                </section>

                <Services />

                <Features />

                <Descriptions />

                <Pricing />

                <Team />

                <Testi />

                <Started />

                <Blog />

                <Contact />

                <SocialMedia />

                <Footer />

                <FooterLinks />

            </div>
        );
    }
}

export default App;



